"use strict";

const getRawData = async () => {
	let response = await fetch("/api/raw");
	let data = await response.json();

	if (response.ok) {
		return data;
	} else {
		throw data;
	}
};

const getGraphs = async (from, to, list, table) => {
	let response = await fetch(`/api/values?from=${from}&to=${to}&params=${list}&table=${table}`);
	let data = await response.json();

	if (response.ok) {
		return data;
	} else {
		throw data;
	}
};

export { getRawData, getGraphs };
