import React from "react";
import "./App.css";
import Control from "./customComponents/Control.js";
import Graphs from "./customComponents/Graphs.js";
import TableRaw from "./customComponents/TableRaw.js";
import Header from "./customComponents/Header.js";
import * as Api from "./api.js";

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			data: {},
			viewLines: [],
			viewMode: "graph",
			loading: true,
			error: false,
			showBar: false
		};
	}

	render() {
		return <div className="App container-fluid px-xl-3">{this.iserror()}</div>;
	}

	iserror() {
		if (this.state.error) return <p>Errore con il server</p>;
		else
			return (
				<>
					{/*<Control changeMode={this.changeMode} />*/}
					<Header mode={this.state.viewMode} showSidebar={this.showSidebar} />
					{this.state.loading ? (
						<>
							<div className="d-flex justify-content-center mt-5">
								<div className="spinner-border" role="status">
									<span className="sr-only"></span>
								</div>
							</div>
						</>
					) : (
						<>{this.viewThing()}</>
					)}
				</>
			);
	}

	viewThing() {
		switch (this.state.viewMode) {
			case "graph":
				return (
					<div className="row pr-2 pr-xl-5">
						<Graphs data={this.state.data} viewLines={this.state.viewLines} />
					</div>
				);
			case "raw":
				return (
					<>
						<TableRaw name="Dati" data={this.state.data.data} /> <div className="py-5"></div>
						<TableRaw name="Variazione" data={this.state.data.variation} />{" "}
					</>
				);
			default:
				return "";
		}
	}

	showSidebar = () => {
		this.setState((state) => {
			return { showBar: !state.showBar };
		});
	};

	componentDidMount() {
		this.changeMode("graph");
	}

	changeMode = (mode) => {
		this.setState({ loading: true });
		if (mode == "raw") {
			Api.getRawData().then((data) => {
				this.setState({ data: data, loading: false, viewLines: [], viewMode: "raw" });
			});
		} else {
			if (mode == "graph") {
				Api.getGraphs("2020-02-24", "2020-10-29", "Deceduti,Positivi", "STORICO").then((data) => {
					this.setState({
						data: data,
						loading: false,
						viewLines: this.getViewLines(data),
						viewMode: "graph"
					});
					//this.setState({ data: data, loading: false, viewLines: [], viewMode: "graph" });
				});
			}
		}
	};

	getViewLines = (data) => {
		let array = Object.keys(data[Object.keys(data)[0]][0]);
		array.splice(array.indexOf("data"), 1);
		return array.map((region) => {
			let color;
			switch (region) {
				case "Abruzzo":
					color = "#C00000";
					break;
				case "Basilicata":
					color = "#FF0000";
					break;
				case "Calabria":
					color = "#C55A11";
					break;
				case "Campania":
					color = "#FF9900";
					break;
				case "Emilia-Romagna":
					color = "#FFD966";
					break;
				case "Friuli-Venezia-Giulia":
					color = "#9FBA16";
					break;
				case "Lazio":
					color = "#548235";
					break;
				case "Liguria":
					color = "#004C00";
					break;
				case "Lombardia":
					color = "#009A46";
					break;
				case "Marche":
					color = "#0A7C66";
					break;
				case "Molise":
					color = "#08B896";
					break;
				case "Piemonte":
					color = "#00B0F0";
					break;
				case "Puglia":
					color = "#0070C0";
					break;
				case "Sardegna":
					color = "#3333CC";
					break;
				case "Sicilia":
					color = "#002060";
					break;
				case "Toscana":
					color = "#7030A0";
					break;
				case "Trentino-Alto-Adige":
					color = "#7030A0";
					break;
				case "Umbria":
					color = "#D60093";
					break;
				case "Valle-d_Aosta":
					color = "#FF00FF";
					break;
				case "Veneto":
					color = "#FF7DFF";
					break;
				default:
					color = "#000";
					break;
			}

			if (region == "Italia") {
				return {
					name: region,
					show: true,
					color: color
				};
			} else {
				return {
					name: region,
					show: false,
					color: color
				};
			}
		});
	};
}

export default App;
