import React from "react";

class Header extends React.Component {
	render() {
		return (
			<>
				<div className="rounded green mt-2 control-panel p-2 align-items-center justify-content-center d-none d-md-flex">
					<button className="btn btn-success border mr-auto" onClick={this.props.showSidebar}>
						{"\u00A0"}Apri menu{"\u00A0"}
					</button>

					<h1 className="inline">Dati pandemia Covid-19</h1>
					<button
						type="button"
						className={
							this.props.mode == "raw"
								? "btn btn-success border ml-auto invisible"
								: "btn btn-success border ml-auto"
						}
						data-toggle="modal"
						data-target="#selectData"
					>
						Cambia dati
					</button>
				</div>
				<div className="rounded green mt-2 control-panel p-2 justify-content-center d-lg-none">
					<div className="col-12">
						<h1>Dati pandemia Covid-19</h1>
					</div>
					<div className="col-12 align-items-center d-flex">
						<button className="btn btn-success border mr-auto" onClick={this.props.showSidebar}>
							{"\u00A0"}Apri menu{"\u00A0"}
						</button>
						<button
							type="button"
							className={
								this.props.mode == "raw"
									? "btn btn-success border ml-auto invisible"
									: "btn btn-success border ml-auto"
							}
							data-toggle="modal"
							data-target="#selectData"
						>
							Cambia dati
						</button>
					</div>
				</div>
			</>
		);
	}
}

export default Header;
