import React from "react";

class Control extends React.Component {
	render() {
		return (
			<div className="rounded green mt-2 control-panel p-2">
				<h1>Dati pandemia Covid-19</h1>
				<div className="col-xl-4">
					<h5>Visualizza come:</h5>
					<div className="btn-group btn-group-toggle" data-toggle="buttons">
						<label className="btn btn-secondary border-dark active">
							<input type="radio" defaultChecked={true} onClick={()=>{this.props.changeMode("raw")}} />
							Database
						</label>
						<label className="btn btn-secondary border-dark">
							<input type="radio" onClick={()=>{this.props.changeMode("graph")}}/>
							Grafici
						</label>
					</div>
				</div>
			</div>
		);
	}
}

export default Control;
